.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-footer {
  position: absolute;
  width: 100%;
  bottom: 0%;

  background-color: #8b0900;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Onboard-body {
  bottom: 50%;
  position: absolute;
  text-align: center;

  align-items: center;
  width: 100%;


}

.hint-caption {
  animation: text-slide-up-empty-full 2s;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}