@import url(https://fonts.googleapis.com/css?family=Lora);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-footer {
  position: absolute;
  width: 100%;
  bottom: 0%;

  background-color: #8b0900;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Onboard-body {
  bottom: 50%;
  position: absolute;
  text-align: center;

  -webkit-align-items: center;

          align-items: center;
  width: 100%;


}

.hint-caption {
  -webkit-animation: text-slide-up-empty-full 2s;
          animation: text-slide-up-empty-full 2s;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.Story-card {
  font-family: 'Lora', serif;
  height: 80vh;
  max-height: 800px;
  width: 80vw;
  top: 10%;
  bottom: 10%;
  left: 0;
  right: 0;
  position: absolute;
  margin: 0 auto;
  text-align: center;
  background-color: aliceblue;
  border-radius: 15px;
  -webkit-box-shadow: 0px 10px 30px 0px;
  -webkit-animation: slideup-card 1.5s;
          animation: slideup-card 1.5s; }
  .Story-card .children {
    padding: 10px;
    height: -webkit-fill-available;
    overflow: auto;
    max-height: 800px;
    max-width: 1000px; }

.Story-text {
  text-align: justify;
  line-height: 200%;
  margin: 30px; }

.App-footer {
  height: 7.5vmin; }

